import React, { useContext, useState } from "react";
import LoginContext from '../../components/Login/LoginContext';
import { useNavigate } from 'react-router-dom';
import '../../css/Login.css';

const Login = () => {
  const { loginMember, setLoginMember } = useContext(LoginContext);
  
  const [memberId, setMemberId] = useState('');
  const [memberPw, setMemberPw] = useState('');

  const navigate = useNavigate();

  const handleLogin = (event) => { 
    event.preventDefault();

    if (loginMember) {
      alert('  ');
      navigate('/');
      return;
    }

    fetch('http://localhost:9090/members/login', {
      method : "POST",
      headers : {"Content-Type" : "application/json",
                 "Accept" : "application/json" },
      body : JSON.stringify({memberId : memberId, memberPw : memberPw})
    })
    .then(resp => resp.json())
    .then(map => {
      console.log(map);

      if (map.loginMember === null) {
        alert('    ');
        return;
      }

      setLoginMember(map.loginMember);

      setMemberId('');
      setMemberPw('');

      navigate('/');
    })
    .catch(error => {
      console.error('Error:', error);
      alert('   .');
    });
  }

  /*
  const logout = () => { setLoginMember(null); }
  <button onClick={logout}></button>
  */

  return (
      <div className="login-container">
        <div className="login-form">
            <h1>!</h1>
            <form className="login-input">
              <div>
                <p><strong></strong></p>
                <input type="text"
                          onChange={e => setMemberId(e.target.value)}
                          value={memberId}
                          placeholder=" "
                          required />
              </div>
              <div>
              <p><strong></strong></p>
                <input type="password"
                          onChange={e => setMemberPw(e.target.value)}
                          value={memberPw}
                          placeholder=" "
                          required />
              </div>
              <div className="button-container">
                <button onClick={handleLogin} >Login</button>
              </div>
            </form>
        </div>
        <div className="login-image">
          <img src="/images/team/login-image.jpg" alt="login-image"/>
        </div>
      </div>
  );
  
};

export default Login;
