import React, {useContext} from 'react';
import {Link, useNavigate} from "react-router-dom";
import '../../css/Header.css'
import LoginContext from '../../components/Login/LoginContext';

const Header = () => {
    const { loginMember, setLoginMember } = useContext(LoginContext);
    const navigate = useNavigate();

    const handleLogout = () => {
        setLoginMember(null);
        localStorage.removeItem('loginMember');
        
        navigate('/');
      };

    return ( 
        <header>
            <div id="login-signup">
                <ul id="login-banner">
                    {loginMember ? (
                        <li>
                            <span>, <strong>{loginMember.memberName}</strong> </span>
                            <a onClick={handleLogout}></a>
                        </li>
                    ) : (
                        <>
                            <li>
                                <Link to="/login"></Link>
                            </li>
                            <li>
                                <Link to="/members/signup"></Link>
                            </li>
                        </>
                    )
                    }

                </ul>
            </div>
            <div id="menu">
                <ul id="main-banner">
                    <li>
                        <Link to="/">PERFECT GAME</Link>
                    </li>
                </ul>
                <ul id="link-banner">
                    <li>
                        <Link to="#"></Link>
                        <ul id="ul-num-1">
                            <li><Link to="/weather"> </Link></li>
                            <li><Link to="/foodmain"> </Link></li>
                        </ul>
                    </li>
                    <li>
                        <Link to="#"></Link>
                    </li>
                    <li>
                        <Link to="/goods"></Link>
                    </li>
                    <li>
                        <Link to="/board"></Link>
                        <ul id="ul-num-2">
                            <li><Link to="#"></Link></li>
                            <li><Link to="/board/team"> </Link></li>
                            <li><Link to="/board/guestbook"></Link></li>
                            <li><Link to="#">, </Link></li>
                        </ul>
                    </li>
                    <li>
                        <Link to="#"></Link>
                        <ul id="ul-num-3">
                            <li><Link to="/mypage"> </Link></li>
                            <li><Link to="#"> </Link></li>
                            <li><Link to="#"> </Link></li>
                            <li><Link to="/mypage/delete"> </Link></li>
                        </ul>
                    </li>
                </ul>
            </div>
        </header>
    )
}
export default Header;