import React, { useState, useContext } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import LoginContext from '../Login/LoginContext'; //   
import "../../css/GoodsDetail.css";

const GoodsDetail = ({ goods }) => {
  const { goodsId } = useParams(); // URL goodsId .
  const navigate = useNavigate(); // useNavigate  =  
  const { loginMember } = useContext(LoginContext); // LoginContext    
  const [quantity, setQuantity] = useState(1); //   1 

  console.log("goodsId:", goodsId);

  // goods   goodsId   .
  const item = goods.find((good) => good.goodsId === Number(goodsId));

  // goodsDetailImage  .
  const detailImages =
    item && item.goodsDetailImage ? JSON.parse(item.goodsDetailImage) : [];

  //   
  const increaseQuantity = () => {
    setQuantity(quantity + 1);
  };

  //   
  const decreaseQuantity = () => {
    if (quantity > 1) {
      //  1   
      setQuantity(quantity - 1);
    }
  };

  //   
  const totalPrice = item ? item.goodsPrice * quantity : 0;

  //  3  ,  
  const formatCurrency = (amount) => {
    return amount.toLocaleString();
  };

  //      
  const handleOrder = async () => {
    if (!loginMember) {
      alert(' .   .');
      navigate('/login');
      return;
    }

    if (item) {
      const orderData = {
        goodsId: item.goodsId,
        orderCount: quantity,
        orderPrice: totalPrice
        
      };

      try {
        const response = await fetch('/goods/order', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(orderData)
        });

        if (response.ok) {
          //    
          alert(" .");
          navigate("/goods/orders", { state: { item, quantity, totalPrice } }); // order   
        } else {
          //  
          alert("    .");
        }
      } catch (error) {
        //   
        console.error("  :", error);
        alert("     .");
      }
    }
  };

  return (
    <div className="goodsDetail-container mt-5">
      {item ? (
        <div className="goods-detail">
          <div className="goods-detail-content">
            <img
              src={`/images/${item.goodsImage}`}
              className="goods-detail-img"
              alt={item.goodsName}
            />
            <div className="goods-detail-info">
              <h2 className="mt-3">{item.goodsName}</h2>
              <h5>: {formatCurrency(item.goodsPrice)}</h5>
              <div className="quantity-selector">
                <button onClick={decreaseQuantity}>-</button>
                <span>{quantity}</span>
                <button onClick={increaseQuantity}>+</button>
              </div>
              <p className="totalPrice"> : {formatCurrency(totalPrice)}</p>
              <button className="buy-button" onClick={handleOrder}></button>
            </div>
          </div>
          <br />
          <hr />
          <h4 className='show-detail-h4'>show detail</h4>
          <hr />
          <div className="goods-show-detail">
            {detailImages.length > 0 ? (
              detailImages.map((image, index) => (
                <img
                  key={index}
                  src={`/images/${image}`}
                  className="goods-show-detail-img"
                  alt={`Detail ${index + 1}`}
                />
              ))
            ) : (
              <p>  .</p>
            )}
          </div>
        </div>
      ) : (
        <p>   .</p>
      )}
    </div>
  );
};

export default GoodsDetail;
