import React from 'react';
import {Link} from "react-router-dom";
import '../../css/Sidebar.css';

const Sidebar = () => {
    return (
        <div className='sidebar-container'>
            <Link to="/board" className='sidebar-board-link'></Link>
            <hr className='sidebar-hr'/>
            <ul className='sidebar-link-list-ul'>
                <li className='sidebar-link-list-li'><Link to="#"></Link></li>
                <li className='sidebar-link-list-li'><Link to="/board/team"> </Link></li>
                <li className='sidebar-link-list-li'><Link to="/board/guestbook"></Link></li>
                <li className='sidebar-link-list-li'><Link to="#">, </Link></li>
            </ul>
        </div>
    )
}

export default Sidebar;