import React, { useState, useEffect } from "react";
import "../../css/foodDetail.css";
import axios from "axios";
import { useParams } from "react-router-dom";
import FoodComment from "./foodComment"; // FoodComment  

function FoodDetail() {
  const { foodId } = useParams();
  const [food, setFood] = useState(null); //   null 
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");

  useEffect(() => {
    const fetchFoodData = async () => {
      try {
        console.log(`Fetching data for foodId: ${foodId}`);
        const response = await axios.get(`http://localhost:9090/foods/${foodId}`);
        console.log("Response data:", response.data);
        setFood(response.data);
      } catch (error) {
        console.error("    :", error);
        setError(error);
      } finally {
        setLoading(false);
      }
    };

    fetchFoodData();
  }, [foodId]);

  if (loading) {
    return <p> ...</p>;
  }

  if (error) {
    return <p>    : {error.message}</p>;
  }

  // food   
  if (!food) {
    return <p>   .</p>;
  }

  return (
    <div className="food">
      <div className="playground-container">
        <div id="playground">
          <p>
            <strong>{food.foodName}</strong>
          </p>
        </div>
        {food.foodsImage && (
          <img
            src={`/images/${food.foodsImage}`}
            alt={food.foodName}
            className="food-image"
            onError={() =>
              console.error("    .")
            }
          />
        )}
        <div className="foodstore-container">
          {food.foodMenu &&
            Object.entries(JSON.parse(food.foodMenu)).map(([menu, price]) => (
              <div key={menu}>
                <p>
                  {menu}: {price}
                </p>
              </div>
            ))}
        </div>
      </div>

      <div className="foodComment-container">
        <FoodComment foodId={foodId} />
      </div>
    </div>
  );
}

export default FoodDetail;
